.doc__wrapper{
  margin-top: 12rem;
}

.doc__top{
  margin: 4rem 0;
}

.main__picture{
  background-size: cover;
  background-position: center;
  height: 50vh;
  width: 100%;
}

.main__location{
  text-align: center;
}

.us-1{
  background-image: url(images/us.jpg);
}

.us-2{
  background-image: url(images/u-2.jpg);
}

.showing-enter{
	opacity: 0;
}

.showing-enter-active{
	opacity: .5;
	transition: opacity 300ms;
}

.showing-enter-done{
	opacity: 1;
}

.showing-exit{
	opacity: 1;
}

.showing-exit-active{
	opacity: .5;
	transition: opacity 300ms;
}

.showing-exit-done{
	opacity: 0;
}

.secondary__heading{
  text-align: center;
  font-size: 3.4rem;
  letter-spacing: 3px;
  margin: 2rem 0;
}

.main__images{
  position: relative;
  height: 20rem;
}

.main__images img{
  height: 100%;
  position: absolute;
}

.main__images--1{
  left: 15rem;
  top: 55%;
  z-index: 998;
}

.main__images--2{
    right: 0;
    top: 0;
    z-index: 2;
}

.main__images--3{
  left: 70%;
  top: 20rem;
  z-index: 99;
}

.location{
  padding: 15rem 0;
    margin-top: -15vh;
}

.come__btn{
  display: inline-block;
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 1px solid transparent;
  padding: .375rem .75rem;
  line-height: 1.5;
  border-radius: .25rem;
  transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
  background-color: #fff0db;
  color: #0a0a0a;
  font-size: 1.6rem;
}

.come__btn:hover{
  cursor: pointer;
  background-color: #0a0a0a;
  color: #fff;
  border-color: #fff;
}

.come__btn .fas{
  color: #0a0a0a;
  transform: rotate(0);
  transition: transform .5s;
  margin-left: .75rem;
}

.come__btn:hover .fas{
  color: #fff;
  transform: rotate(90deg);
}


@media only screen and (max-width: 1200px){
  .main__images{
    height: 17.5rem;
  }
  .main__images--1 {
    left: 10rem;
  }
  .main__images--3 {
    left: 64%;
    top: 16rem;
  }
  .main__images--2 {
    right: 5px;
  }
}

@media only screen and (max-width: 1010px){
  .main__images{
    height: 15rem;
  }
  .main__images--1 {
    left: 8rem;
  }
  .main__images--3 {
    left: 60%;
    top: 13rem;
  }
  .main__images--2 {
    right: 8px;
  }
}


@media only screen and (max-width: 885px){
  .main__images{
    height: 12.5rem;
  }
  .main__images--1 {
    left: 6rem;
  }
  .main__images--3 {
    left: 56%;
    top: 10rem;
  }
  .main__images--2 {
    right: 50px;
  }
}

@media only screen and (max-width: 767px){
  .main__images {
    height: 14.5rem;
  }
  .main__images--1 {
    top: -25px;
    left: 15%;
  }
  .main__images--2{
    left: 38%;
    z-index: 999;
    top: -15px;
  }
  .main__images--3 {
    left: 60%;
    top:-25px;
  }
  .location{
    padding-bottom: 2rem;
  }
}

@media only screen and (max-width: 660px){
