.formLab{
  align-self: flex-end;
  margin-right: 1.6rem;
}

.form-group{
  margin-right: 2.2rem;
}

.form__label{
    background: #f9f9f9;
    border-bottom: 1px solid #e99ba6;
}

.form__input{
  background: #f9f9f9;
  border: none;
  padding: .5rem;
}

.underline{
  width: 0;
  height: 2px;
  display: block;
  background: #972426;
  transition: all .3s ease;
}
.form__input:focus{
  outline: none;
  border-bottom: 0;
}

input:focus ~ .underline{
  width: 100%;
}


.checkContainer{
  position: relative;
  padding-left: 4rem;
  margin-right: 3rem;
  cursor:pointer;
}
.customCheck{
  height: 3rem;
  width: 3rem;
  background: #f7f7f7;
  display: block;
  border: 1px solid #bdbdbd;
  position: absolute;
  top: -3px;
  left: -1rem;
  cursor: pointer;
}

.customCheck:after{
  content: '\f00c';
  font-family: "Font Awesome 5 Free"; font-weight: 900;
  display: block;
  color: #7e7e7e;
  font-size: 2rem;
  opacity: 0;
  transition: all .3s;
  position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.customCheck.no:after{
  content: '\f00d';
}
.attRadio{
  display: none;
  opacity: 0;
}
.attRadio:checked ~ .customCheck.no{
  background-color: #972426;
  border-color: #972426;
}

.attRadio:checked ~ .customCheck.no:after{
  color: #fff;
  opacity: 1;
}

.attRadio:checked ~ .customCheck.yes{
  background-color: #fff;
  border-color: #ffaaa5;
}

.attRadio:checked ~ .customCheck.yes:after{
  color: #ffaaa5;
  opacity: 1;
}

.customCheck:hover:after{
  opacity: 1;
}

.form__wrap{
  background-color: #fff;
}

.form__input--button{
    display: inline-block;
    font-weight: 400;
    color: #fff;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    background-color: #972426;
    font-size: 2rem;
}

.form__input--button:hover{
  color: #972426;
  border-color: #972426;
  background-color: #fff;
}

.form__section{
  padding: 15rem 0;
}


.form__card{
  background-image: url(images/us-4.jpg);
  background-size: cover;
  background-position: 0% 15%;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.15)
}

.form__card--content{
  height: 100%;
  background-color: #fff;
}

.form__card--wrapper{
  padding: 3rem 2rem;
}

.half-vh {
  min-height: 69vh;
}

.form__img{
  background-image: url(images/us-4.jpg);
  background-size: 100% 100%;
  background-position: 0% 20%;
  background-repeat: no-repeat;
  height: 100%;
  width: 100%;
}

.form__form{
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.form__wrapper{
    max-width: 1100px;
    margin: 0 15px 0 10px;
}

@media only screen and (max-width: 767px){
  .form__section {
    padding: 8rem 0;
  }
  .half-vh {
    min-height: 200vh;
  }
  .form__form{
    justify-content: flex-start;
    height: 80%;
  }
}

@media only screen and (max-width: 631px){
  .half-vh {
    min-height: 155vh;
  }
}
