.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
  z-index: 999999;
}

.header .container{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.header{
  font-size: 3.6rem;
}

.header__left{
  margin-right: 2.5rem;
  margin-left: auto;
}

.header__right{
  margin-left: 2.5rem;
  margin-right: auto;
}

.location__wrap{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 575px){
  .header__right{
    margin-left: 1.5rem;
  }
  .header__left{
    margin-right: 1.5rem;
  }
}

@media only screen and (max-width: 460px){
  .header__right{
    margin-right: 1rem;
  }
  .header__left{
    margin-left: 1rem;
  }
  .header .container {
    padding: 2rem 0;
  }
}
@media only screen and (max-width: 438px){
  .header__img{
    width: 75%;
    margin: 0 auto;
  }

  .header__center{
    margin: 0 auto;
  }

  .header__right,
  .header__left{
    margin: 0 .2rem;
  }

  .header__img img{
    max-width: 100%;
  }
}

@media only screen and (max-width: 346px){
  .header__img{
    width: 70%;
  }
}

@media only screen and (max-width: 346px){
  .header__img{
    width: 65%;
  }
}
