.timer__wrap{
  margin: 0 2rem;
  text-align: center;
  position: relative;
}

.timer__wrap:not(:last-child) .timer__time:after{
  content: ":";
  display: inline;
  position: absolute;
  top: 0;
  left: 125%;
}

@media only screen and (max-width: 372px){
  .timer__wrap{
    margin: 0 1.2rem;
  }
}
