/* FONTS  */
/* MAIN TEXT - font-family: 'Fauna One', serif; */
/* HEADINGS - font-family: 'Playfair Display', serif; */

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

header:focus,
section:focus {
  outline: none; }

html {
  overflow-x: hidden;
  /* default browser font size is 16px 1rem = 10px */
  font-size: 62.5%; }

body {
  margin: 0;
  font-family:'Fauna One', serif;
  background-color: #ffd9de;
  background-image: linear-gradient(88deg, #ffd9de 0%, #e99ba6 100%);
  font-size: 1.6rem;
}

h1, h2, h3, h4, h5,
.h1, .h2, .h3, .h4, .h5{
  font-family: 'Playfair Display', serif;
}

h1{
  font-size: 3rem;
}


h2{
  font-size: 2.6rem;
}

h3{
  font-size: 2.4rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

