@media only screen and (max-width: 438px){
  h1 {
    font-size: 2.6rem;
  }
}

@media only screen and (max-width: 373px){
  h1 {
    font-size: 2.2rem;
  }
}

@media only screen and (max-width: 362px){
  body{
    overflow-x: hidden;
  }
  h1 {
    font-size: 2rem;
}
}

@media only screen and (max-width: 332px){
  body{
    overflow-x: hidden;
  }

  h1 {
    font-size: 1.6rem;
  }
}
